import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Table from "@mui/material/Table";

const dispatchedEvents = [];

const SrsMuiTable = function (props, ref) {
  return (
    <Table {...props} theme={props.muiTheme}>
      <slot />
    </Table>
  );
};

const styles = `
  /* Custom styles for the MuiTable component */
`;

const props = [
  {
    name: "size",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "aria-label",
    defaultValue: null,
    type: "string",
  },
  {
    name: "stickyHeader",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Table if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-table-row"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTable),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
